import { Injectable } from '@angular/core';
import Swal, { SweetAlertOptions } from 'sweetalert2';

@Injectable({ providedIn: 'root' })
export class AlertHelpersService {
  constructor() {}

  async showAlert({
    title,
    icon,
    showCancelButton = false,
    confirmButtonColor = '',
    confirmButtonText = 'Ok',
    customClass = {
      confirmButton: 'btn bg-primary-web text-white px-5',
      cancelButton: 'btn bg-common-dark text-white px-5'
    },
    cancelButtonText = 'No',
    cancelButtonColor = '',
    showClass = {
      popup: 'animated fadeInDown faster'
    },
    hideClass = {
      popup: 'animated fadeOutUp faster'
    },
    reverseButtons = true
  }: SweetAlertOptions): Promise<any> {
    return await Swal.fire({
      title,
      icon,
      showCancelButton,
      confirmButtonColor,
      confirmButtonText,
      customClass,
      cancelButtonText,
      cancelButtonColor,
      showClass,
      hideClass,
      reverseButtons
    });
  }
}
