import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DetailsComponent } from './details.component';

import { FileInfoComponent } from './tabs/file-info/file-info.component';
import { EditComponent } from './tabs/edit/edit.component';
import { TranscriptComponent } from './tabs/transcript/transcript.component';
import { AuditTrailComponent } from './tabs/audit-trail/audit-trail.component';
import { canDeactivateGuard } from './tabs/edit/edit.guard';

const routes: Routes = [
  {
    path: '',
    component: DetailsComponent,
    children: [
      { path: 'file-info', component: FileInfoComponent },
      { path: 'edit', component: EditComponent, canDeactivate: [canDeactivateGuard] },
      { path: 'transcript', component: TranscriptComponent },
      { path: 'audit-trail', component: AuditTrailComponent },
      { path: '', pathMatch: 'full', redirectTo: 'file-info' }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DetailsRoutingModule {}
