import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { COCLogApiService } from 'src/app/api';

import { FileLocation } from 'src/app/constants/file-locations';
import { MediaPlayerComponent } from 'src/app/modules/shared/components/media-player/media-player.component';

import { DetailsService } from './details.service';
import { MediaDataService } from 'src/app/modules/shared/services/media-data.service';
import { EditService } from './tabs/edit/edit.service';

@Component({
  selector: 'app-details',
  standalone: false,
  templateUrl: './details.component.html',
  styleUrl: './details.component.scss'
})
export class DetailsComponent {
  @ViewChild(MediaPlayerComponent) mediaPlayer!: MediaPlayerComponent;

  isLoading = false;
  fileLocations = FileLocation.locations;

  constructor(
    private router: Router,
    private cocLogAPIService: COCLogApiService,
    private detailsService: DetailsService,
    private mediaDataService: MediaDataService,
    private editService: EditService
  ) {
    const navigation = router.getCurrentNavigation();

    if (navigation?.extras.state) {
      const session_id = navigation.extras.state['sid'];
      const file_id = navigation.extras.state['fid'];
      const file_type = navigation.extras.state['f_type'];

      // Update data in service
      detailsService.selected_session = session_id;
      detailsService.selected_file = file_id;
      detailsService.selected_file_type = file_type;

      // Set file visibility condition
      detailsService.canShowFile =
        file_type.includes('video') || file_type.includes('audio')
          ? this.detailsService.session_rec_object.fileLocation === this.fileLocations.dock ||
            this.detailsService.session_rec_object.fileLocation === this.fileLocations.central
          : this.detailsService.session_photo_object.fileLocation === this.fileLocations.dock ||
            this.detailsService.session_photo_object.fileLocation === this.fileLocations.central;

      // Set file urls
      if (file_type.includes('video')) {
        const rec = this.detailsService.session_rec_object;

        // this.mediaDataService.videoData = {
        //   videoSource: rec['<ResourceURL>k__BackingField'],
        //   videoThumbnail: ''
        // };
      } else if (file_type.includes('audio')) {
        const rec = this.detailsService.session_rec_object;

        // this.mediaDataService.audioData = {
        //   audioSource: rec['<ResourceURL>k__BackingField']
        // };
      } else {
        const photo = this.detailsService.session_photo_object;

        // this.mediaDataService.photoData = {
        //   imageSource: photo['<ResourceURL>k__BackingField']
        // };
      }
    }
  }

  get file_type() {
    return this.detailsService.selected_file_type;
  }

  get photo_data() {
    return this.mediaDataService.photoData;
  }

  get canShowFile() {
    return this.detailsService.canShowFile;
  }

  get isFileError() {
    return this.detailsService.isFileError;
  }

  get isEditTabAvailable() {
    const can_edit = this.file_type.includes('video') || this.file_type.includes('audio');
    return can_edit;
  }

  isLinkActive(url: any) {
    const baseUrl = this.router.url.replace('/AngularSession.aspx', '');
    return baseUrl === url;
  }

  loadEvent(state: boolean) {
    this.isLoading = state;
  }

  logsEvent(selectingFile: boolean) {
    this.fetchCOCLogs();
    if (selectingFile) this.mediaPlayer.onSelectSessionFile();
  }

  fetchCOCLogs() {
    this.detailsService.isLogsLoaded = false;
    this.cocLogAPIService.apiCOCLogGetCOCLogByUniqueFileName(this.detailsService.selected_file).subscribe((logs) => {
      this.detailsService.cocLogs = logs;
      this.detailsService.isLogsLoaded = true;
    });
  }

  ngAfterViewInit() {
    // setTimeout(() => {
    //   window.scrollTo(1900, 840);
    // }, 2000);
  }

  ngOnInit() {
    this.fetchCOCLogs();
  }
}
