import { Component, inject } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';

import { FileLocation } from 'src/app/constants/file-locations';
import { FileLocationNamesTypes } from 'src/app/modules/shared/models/file-locations.model';

import { CategoryApiService, TaggedSessionApiService } from 'src/app/api';
import { CategoriesModel } from 'src/app/api/model/category.model';
import { TagSessionRequest } from 'src/app/api/model/tagged-session.model';
import { SessionGroupsModel } from 'src/app/api/model/session.model';

import { DetailsService } from '../details.service';

interface MetaData {
  title: string;
  content: string;
  icon?: boolean;
}

@Component({
  selector: 'session-metadata',
  standalone: false,
  templateUrl: './session-metadata.component.html',
  styleUrl: './session-metadata.component.scss'
})
export class SessionMetadataComponent {
  private readonly detailsService = inject(DetailsService);
  fileLocationNames = FileLocation.location_names;

  metadataExpanded = false;
  formGroup!: FormGroup;

  initialFormValues: any;
  isFormModified = false;

  categoryList: string[] = [];
  categoryIdList: number[] = [];
  selectedCategory: string = '';

  constructor(
    private categoryApiService: CategoryApiService,
    private taggedSessionApiService: TaggedSessionApiService
  ) {
    categoryApiService.apiCategoryGetCategories().subscribe({
      next: (res: CategoriesModel[]) => {
        res.forEach((cat) => {
          this.categoryList.push(cat.CategoryName);
          this.categoryIdList.push(cat.ID);
        });
      },
      error: (error: Error) => {
        console.log('Category API error: ', error);
      }
    });
  }

  get session(): SessionGroupsModel {
    return this.detailsService.session_object;
  }

  get metadata(): MetaData[] {
    return [
      {
        title: 'Created by',
        content: this.session['<CreatorUser>k__BackingField']._displayName
      },
      {
        title: 'Device serial',
        content: this.session['<CameraAssetTag>k__BackingField']
      },
      {
        title: 'Camera MAC ID',
        content: this.session._camera_mac
      },
      {
        title: 'Number of files',
        content: (this.session._recordings.length + this.session._photos.length).toString()
      },

      {
        title: 'Starts',
        content: `${this.date} ${this.startTime}`
      },
      {
        title: 'Finishes',
        content: `${this.date} ${this.finishTime}`
      },
      {
        title: 'Created At',
        content: `${this.createdDate} ${this.createdTime}`
      },
      {
        title: 'Delete On',
        // content: this.deleteOnDate
        content: `${this.createdDate} ${this.createdTime}`
      },

      {
        title: 'Stealth',
        content: this.session['<IsStealth>k__BackingField'].toString(),
        icon: true
      },
      {
        title: 'Pre-record',
        content: this.session._containsPrerecord.toString(),
        icon: true
      },
      {
        title: 'Streamed',
        content: 'false',
        icon: true
      },
      {
        title: 'GPS Track',
        content: (this.session._GPSData.length !== 0).toString(),
        icon: true
      }
    ];
  }

  get username() {
    const file_type = this.detailsService.selected_file_type;

    if (file_type.includes('video') || file_type.includes('audio'))
      return this.detailsService.session_rec_object['<OwnerName>k__BackingField'];
    else return this.detailsService.session_photo_object.owneruser;
  }

  get isSessionCategorized() {
    return this.detailsService.session_object['<CategoryName>k__BackingField'] !== '';
  }

  get category() {
    return this.detailsService.session_object['<CategoryName>k__BackingField'];
  }

  get server_location() {
    const file_type = this.detailsService.selected_file_type;

    if (file_type.includes('video') || file_type.includes('audio'))
      return this.fileLocationNames[this.detailsService.session_rec_object.fileLocation as FileLocationNamesTypes];
    else return this.fileLocationNames[this.detailsService.session_photo_object.fileLocation as FileLocationNamesTypes];
  }

  get createdTime() {
    const time = this.session._create_time.split('T')[1].split('.')[0];
    return time;
  }

  get createdDate() {
    const date = this.session._create_time.split('T')[0].replaceAll('-', '/');
    return date;
  }

  get startTime() {
    const start_time = this.session._start_time.split('T')[1].split('.')[0];
    return start_time;
  }

  get finishTime() {
    const finish_time = this.session._finish_time.split('T')[1].split('.')[0];
    return finish_time;
  }

  get date() {
    const date = this.session._start_time.split('T')[0].replaceAll('-', '/');
    return date;
  }

  get deleteOnDate() {
    const date = this.session['<DeletedOn>k__BackingField'].split('T')[0].replaceAll('-', '/');
    return date;
  }

  get charLeft(): number {
    return 300 - this.formGroup.controls['description'].value.length;
  }

  onMetadataToggle() {
    this.metadataExpanded = !this.metadataExpanded;
  }

  onInputChange(value: any) {
    this.selectedCategory = value;
    this.formGroup.patchValue({
      category: value
    });
  }

  async onSave() {
    const response = await fetch('https://api.ipify.org');
    const ip = await response.text();

    this.taggedSessionApiService
      .apiTaggedSessionCheckIfSessionTagged(this.detailsService.session_object._vod_id)
      .subscribe({
        next: (IsTagged: boolean) => {
          const body: TagSessionRequest = {
            IsPriorityUpload: this.formGroup.get('priority_upload')?.value ?? false,
            DefaultCategoryID:
              this.categoryIdList[this.categoryList.indexOf(this.session['<CategoryName>k__BackingField'])], // check
            CategoryID: this.categoryIdList[this.categoryList.indexOf(this.formGroup.get('category')?.value)],
            categoryActionType: 'UploadToCentralServer', // check
            IsTagged: IsTagged,
            vodID: this.detailsService.session_object._vod_id,
            sessionID: this.detailsService.session_object._session_id,
            CaseNumber: this.formGroup.get('case_number')?.value,
            Classified: this.formGroup.get('restricted_access')?.value,
            Notes: '', // check
            desc: this.formGroup.get('description')?.value,
            IPAddress: ip,
            name: this.formGroup.get('name')?.value,
            MetaData: '' // check
          };
          console.log(body);

          this.taggedSessionApiService.apiTaggedSessionSaveTaggedSession(body).subscribe({
            next: (response) => {
              console.log(response);
              this.initialFormValues = this.formGroup.getRawValue();
              this.isFormModified = false;
            },
            error: (error: Error) => {
              console.log('Error in tag a session API: ', error);
            }
          });
        },
        error: (error) => {
          console.log('Error in check tagged session API: ', error);
        }
      });
  }

  ngOnInit() {
    this.formGroup = new FormGroup({
      name: new FormControl(this.session._name ?? ''),
      case_number: new FormControl(this.session['<CaseNumber>k__BackingField'] ?? ''),
      category: new FormControl(this.session['<CategoryName>k__BackingField'] ?? ''),
      event_number: new FormControl(''),
      classification: new FormControl(''),
      description: new FormControl(this.session._desc ?? ''),
      victim_poi: new FormControl(''),
      offence: new FormControl(''),
      restricted_access: new FormControl(this.session['<Classified>k__BackingField'] ?? false),
      priority_upload: new FormControl(false)
    });
    this.initialFormValues = this.formGroup.getRawValue();

    this.formGroup.valueChanges.subscribe(() => {
      this.isFormModified = JSON.stringify(this.formGroup.getRawValue()) !== JSON.stringify(this.initialFormValues);
    });

    this.selectedCategory = this.session['<CategoryName>k__BackingField'];
  }
}
