import { Component } from '@angular/core';
import { COCLogApiService } from 'src/app/api/api/coc-log-api.service';
import { COCLogModel } from 'src/app/api/model/coc-log.model';
import { DetailsService } from '../../details.service';

@Component({
  selector: 'app-audit-trail',
  standalone: false,
  templateUrl: './audit-trail.component.html',
  styleUrl: './audit-trail.component.scss'
})
export class AuditTrailComponent {
  constructor(private detailsService: DetailsService) {}

  get isLoaded(): boolean {
    return this.detailsService.isLogsLoaded;
  }

  get cocLogs(): COCLogModel[] {
    return this.detailsService.cocLogs;
  }

  getCreatedTime(log: COCLogModel) {
    const time = log.DateCreated;
    return time;
  }

  ngOnInit() {}
}
