import { Component } from '@angular/core';

@Component({
  selector: 'waveform-loader',
  standalone: false,
  templateUrl: './waveform-loader.component.html',
  styleUrl: './waveform-loader.component.scss'
})
export class WaveformLoaderComponent {
  waveformArray: number[] = [];

  constructor() {
    this.waveformArray = Array.from({ length: 100 }, (_, i) => {
      return Math.floor(Math.random() * 70);
    });
  }
}
