import { PlayerDataModel, TracksDataModel } from '../modules/shared/models/media-player.model';

export class MediaData {
  private static _playerData: PlayerDataModel = {
    isPlaying: false,
    progress: 0,
    loaded: 0,
    current_time: '00:00:00',
    total_time: '00:00:00',
    duration: 0,
    volume: 0.6,
    speed: 1,
    isFullscreen: false
  };

  private static _tracksData: TracksDataModel = {
    fixed_width: 0,
    track_divisions: [],
    track_frames: [],
    trackpad_ruler_width: 0,
    trackpad_content_width: 0,
    trackpad_left_ph_width: 0,
    trackpad_right_ph_width: 0,
    trackpad_cursor_position: 0,
    trackpad_stick_left_position: 0,
    trackpad_stick_right_position: 0,
    trackpad_audio_handle_width: 0,
    trackpad_audio_handle_left_position: 0,
    trackpad_audio_handle_right_position: 0
  };

  constructor() {}

  static resetPlayerData() {
    return this._playerData;
  }

  static resetTracksData() {
    return this._tracksData;
  }
}
