import { Component, EventEmitter, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Subject, takeUntil } from 'rxjs';

import { SessionApiService } from 'src/app/api';
import { SessionGroupsModel, SessionSearchResponseModel } from 'src/app/api/model/session.model';

import { UserSessionService } from 'src/app/core/session/user-session.service';
import { SessionService } from '../../session.service';
import { DetailsService } from '../details.service';
import { MediaDataService } from 'src/app/modules/shared/services/media-data.service';

@Component({
  selector: 'details-header',
  standalone: false,
  templateUrl: './details-header.component.html',
  styleUrl: './details-header.component.scss'
})
export class DetailsHeaderComponent {
  @Output() onLoadingEvent = new EventEmitter<boolean>();
  @Output() logsEvent = new EventEmitter<boolean>();

  private destroy$: Subject<void> = new Subject<void>();

  searchInput: string = '';
  selectedFromDateModel!: Date;
  selectedToDateModel!: Date;

  loggedInUserId: number;
  isLoading = false;

  basicFormGroup = new FormGroup({
    fromDate: new FormControl(new Date(), [Validators.required]),
    toDate: new FormControl(new Date(), [Validators.required])
  });

  constructor(
    private sessionApiService: SessionApiService,
    private userSession: UserSessionService,
    private sessionService: SessionService,
    private detailsService: DetailsService,
    private mediaDataService: MediaDataService
  ) {
    // Reset from, to date
    this.basicFormGroup.setValue({ fromDate: null, toDate: null });
    this.loggedInUserId = this.userSession.userId;
  }

  get session_id() {
    return this.detailsService.session_object._vod_id;
  }

  get username() {
    return this.detailsService.session_object['<CreatorUser>k__BackingField']._displayName;
  }

  get canAddCase(): boolean {
    return this.detailsService.selectedSessions().length !== 0;
  }

  handleFromDateChange() {
    this.selectedFromDateModel = this.basicFormGroup.get('fromDate')?.value!;
  }

  handleToDateChange() {
    this.selectedToDateModel = this.basicFormGroup.get('toDate')?.value!;
  }

  updateSessionData(sessions: SessionGroupsModel[]) {
    this.sessionService.sessionExpandedList = new Array(sessions.length).fill(false);
    this.detailsService.selected_session = sessions[0]._session_id;

    const recordings = sessions[0]._recordings;
    const photos = sessions[0]._photos;

    if (recordings.length !== 0) {
      this.detailsService.selected_file = recordings[0].uniqueFileName;
      this.detailsService.selected_file_type = recordings[0].fileType;

      if (recordings[0].fileType.includes('video')) {
        this.mediaDataService.videoData = {
          videoSource: recordings[0]['<ResourceURL>k__BackingField'],
          videoThumbnail: ''
        };
      } else {
        this.mediaDataService.audioData = {
          audioSource: recordings[0]['<ResourceURL>k__BackingField']
        };
      }
    } else {
      this.detailsService.selected_file = photos[0].uniqueFileName;
      this.detailsService.selected_file_type = 'photo';

      this.mediaDataService.photoData = {
        imageSource: photos[0]['<ResourceURL>k__BackingField']
      };
    }
  }

  filterSessions() {
    const current_date = new Date();

    const few_days_back = new Date(); // to get 7 days before
    few_days_back.setDate(current_date.getDate() - 7);

    this.isLoading = true;
    this.onLoadingEvent.emit(this.isLoading);

    this.sessionApiService
      .apiMediaSearchGetFilterMediaSearch({
        currentPageNo: 1,
        itemsPerPage: 20,
        searchMode: 'Basic',
        userID: -1, // -1 for "All" || '17134' || User ID === Account
        loggedInUserID: this.loggedInUserId,
        combinedSearch: this.searchInput,
        from: this.selectedFromDateModel?.toISOString() ?? current_date,
        to: this.selectedToDateModel?.toISOString() ?? few_days_back,
        includeExpired: false,
        includeLiveSessions: false
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: async (response: SessionSearchResponseModel) => {
          // Set session data
          this.sessionService.totalSessionCount = response.TotalSessionsCount;
          this.sessionService.sessionsList = response.SessionGroups;

          this.updateSessionData(response.SessionGroups);

          this.logsEvent.emit(false);

          // Set Loaders
          this.isLoading = false;
          this.onLoadingEvent.emit(this.isLoading);

          // Reset form
          this.basicFormGroup.reset();
          this.searchInput = '';
        },
        error: (error: Error) => {
          console.log('Session basic search API error: ', error);

          this.isLoading = false;
          this.onLoadingEvent.emit(this.isLoading);
        }
      });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
