import { Component } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { DetailsService } from '../../details.service';
import { CalculationHelpers } from 'src/app/modules/shared/helpers/calculations';

@Component({
  selector: 'app-file-info',
  standalone: false,
  templateUrl: './file-info.component.html',
  styleUrl: './file-info.component.scss'
})
export class FileInfoComponent {
  initialFormValues: any;
  isFormModified = false;

  formGroup: FormGroup = new FormGroup({
    name: new FormControl(''),
    case_number: new FormControl(''),
    event_number: new FormControl(''),
    category: new FormControl(''),
    notes: new FormGroup('')
  });

  constructor(private detailsService: DetailsService, private helpers: CalculationHelpers) {}

  get filename() {
    return this.detailsService.selected_file;
  }

  get filesize() {
    const file_type = this.detailsService.selected_file_type;
    const file_size =
      file_type.includes('video') || file_type.includes('audio')
        ? this.detailsService.session_rec_object.fileSize
        : this.detailsService.session_photo_object.fileSize;

    return this.helpers.bytesToMega(file_size);
  }

  get creator() {
    return this.detailsService.session_object['<CreatorUser>k__BackingField']._displayName;
  }

  get owner() {
    const file_type = this.detailsService.selected_file_type;

    if (file_type.includes('video') || file_type.includes('audio'))
      return this.detailsService.session_rec_object['<OwnerName>k__BackingField'];
    else return this.detailsService.session_photo_object.owneruser;
  }

  ngOnInit() {
    this.initialFormValues = this.formGroup.getRawValue();

    this.formGroup.valueChanges.subscribe(() => {
      this.isFormModified = JSON.stringify(this.formGroup.getRawValue()) !== JSON.stringify(this.initialFormValues);
    });
  }
}
