import { Component, EventEmitter, inject, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { SessionGroupsModel, SessionPhotoModel, SessionRecordingModel } from 'src/app/api/model/models';

import { SessionService } from '../../session.service';
import { DetailsService } from '../details.service';
import { EditService } from '../tabs/edit/edit.service';
import { MediaDataService } from 'src/app/modules/shared/services/media-data.service';

import { SessionConstants, FileLocationNamesTypes } from 'src/app/constants/session-constants';
import { MediaData } from 'src/app/constants/media-data';

import { ConfirmationModalComponent } from 'src/app/modules/shared/components/confirmation-modal/confirmation-modal.component';
import { CalculationHelpers } from 'src/app/modules/shared/helpers/calculations';

@Component({
  selector: 'session-list',
  standalone: false,
  templateUrl: './session-list.component.html',
  styleUrl: './session-list.component.scss'
})
export class SessionListComponent {
  private readonly detailsService = inject(DetailsService);
  private readonly calculationHelpers = inject(CalculationHelpers);

  @Output() logsEvent = new EventEmitter<boolean>();

  sessionExpandedList: boolean[];
  fileLocationNames = SessionConstants.fileLocationNames;
  fileLocations = SessionConstants.fileLocations;

  constructor(
    private dialogRef: MatDialog,
    private sessionService: SessionService,
    private mediaDataService: MediaDataService,
    private editService: EditService
  ) {
    this.sessionExpandedList = Array(this.sessionsList.length).fill(false);
  }

  get sessionsList() {
    return this.sessionService.sessionsList;
  }

  getSessionType(session: SessionGroupsModel): number {
    return session['<SessionType>k__BackingField'];
  }

  getUsername(session: SessionGroupsModel): string {
    return session['<CreatorUser>k__BackingField']._username;
  }

  getDuration(session: SessionGroupsModel): string {
    const start: any = new Date(session._start_time);
    const end: any = new Date(session._finish_time);

    const diff = end.getTime() - start.getTime();
    const calculated_time = this.calculationHelpers.secondsToTime(diff / 1000);

    const final_time = this.calculationHelpers.timeToWords(calculated_time);
    return final_time;
  }

  redactionInProgress(file: SessionRecordingModel) {
    // * Checking if just the file is "redaction in progress"

    const inProgress =
      file['<RedactedStatus>k__BackingField'] !== -1 &&
      file['<RedactedStatus>k__BackingField'] !== 11 &&
      file['<RedactedStatus>k__BackingField'] !== 13;

    return inProgress;
  }

  cantShowFile(file: SessionRecordingModel) {
    return file['<RedactedStatus>k__BackingField'] === 11;
  }

  fileNotExists(file: SessionRecordingModel | SessionPhotoModel) {
    return this.fileLocations.central !== file.fileLocation && this.fileLocations.dock !== file.fileLocation;
    // return this.fileLocations.VET === file.fileLocation || this.fileLocations.kiosk === file.fileLocation;
  }

  retrieveStartTime(session: SessionGroupsModel) {
    const start_time = session._start_time.split('T')[1].split('.')[0];
    return start_time;
  }

  retrieveFinishTime(session: SessionGroupsModel) {
    const finish_time = session._finish_time.split('T')[1].split('.')[0];
    return finish_time;
  }

  retrieveDate(session: SessionGroupsModel) {
    const date = session._start_time.split('T')[0].replaceAll('-', '/');
    return date;
  }

  retrieveServerLocation(file: SessionRecordingModel | SessionPhotoModel) {
    return this.fileLocationNames[file.fileLocation as FileLocationNamesTypes];
  }

  retrieveIsExpanded(index: number) {
    return this.sessionExpandedList[index];
  }

  retrieveIsFileSelected(file: string) {
    const selected = file === this.detailsService.selected_file;
    return selected;
  }

  onDropdownToggle(index: number) {
    this.sessionExpandedList[index] = !this.sessionExpandedList[index];
  }

  openDialog() {
    return this.dialogRef.open(ConfirmationModalComponent, {
      data: {
        title: `You have unsaved changes in this file. Are you sure to discard these changes?`
      }
    });
  }

  updateSelectedFile(session: SessionGroupsModel, file: SessionRecordingModel | SessionPhotoModel, type: string) {
    this.editService.isAudioGenerated = false;

    this.detailsService.selected_session = session._session_id;
    this.detailsService.selected_file = file.uniqueFileName;
    this.detailsService.selected_file_type = type;
    this.detailsService.canShowFile =
      type.includes('video') || type.includes('audio')
        ? this.detailsService.session_rec_object.fileLocation === this.fileLocations.dock ||
          this.detailsService.session_rec_object.fileLocation === this.fileLocations.central
        : this.detailsService.session_photo_object.fileLocation === this.fileLocations.dock ||
          this.detailsService.session_photo_object.fileLocation === this.fileLocations.central;

    // Resetting player data
    this.mediaDataService.playerData = MediaData.resetPlayerData();
    this.mediaDataService.tracksData = MediaData.resetTracksData();

    if (type.includes('video')) {
      this.mediaDataService.videoData = {
        videoSource: file['<ResourceURL>k__BackingField'],
        videoThumbnail: ''
      };
    } else if (type.includes('audio')) {
      this.mediaDataService.audioData = {
        audioSource: file['<ResourceURL>k__BackingField']
      };
    } else {
      this.mediaDataService.photoData = {
        imageSource: file['<ResourceURL>k__BackingField']
      };

      // Only if photo is selected, update the loader of edit UI here..
      this.editService.editObservable = 'loaded';
      this.editService.editObservable = '';
    }

    this.logsEvent.emit(true);
  }

  onSelectSessionFile(session: SessionGroupsModel, file: SessionRecordingModel | SessionPhotoModel, type: string) {
    if (this.fileNotExists(file)) return;

    if (this.editService.edited) {
      this.openDialog()
        .afterClosed()
        .subscribe((result) => {
          if (result) {
            // Updating this to trigger reset in Edit UI
            this.editService.editObservable = 'on_file_select';
            this.editService.editObservable = 'loading';
            this.updateSelectedFile(session, file, type);
          }
        });
    } else {
      this.editService.editObservable = 'loading';
      this.updateSelectedFile(session, file, type);
    }
  }

  renderSelectedSession() {
    const session = this.detailsService.session_object;
    this.sessionExpandedList[this.sessionsList.indexOf(session)] = true;
  }

  ngOnInit() {
    this.renderSelectedSession();
  }
}
